export default {
	"default_date":{
		"display":true,
		"inputType":"datetime",
		"labelText":"Default Date",
		"value":"",
		"tooltip":"The date on which all routes will start"
	},
	"travel_mode":{
		"display":true,
		"inputType":"select",
		"labelText": "Travel Mode",
		"value": "fnGetDefaultMode",
		"domain": "fnGetAllModes",
		"tooltip":"Choose the mode of transportation from your ArcGIS Online Org"
	},
	"time_zone_usage_for_time_fields":{
		"display":true,
		"inputType":"select",
		"labelText":"Time Zone Usage for Time Fields",
		"value": "GEO_LOCAL",
		"domain": [
			{"text":"Local Time", "value":"GEO_LOCAL"},
			{"text":"UTC (GMT + 0:00)", "value":"UTC"}
		],
		"tooltip":"Specify the time zone for the input date-time fields"
	},
	"impedance":{
		"display":false,
		"inputType":"select",
		"labelText":"Impedance",
		"value": "Truck Time",
		"domain": [
			{"text":"Truck Time", "value":"Truck Time"},
			{"text": "Drive Time", "value":"Drive Time"},
			{"text":"Walk Time", "value":"Walk Time"}
		],
		"tooltip":"Impedance for the route. If travel mode is set, this value is overridden. See REST API docs"
	},
	"time_units":{
		"display":true,
		"inputType":"select",
		"labelText": "Time Units",
		"value":"Minutes",
		"domain": [
			{"text":"Seconds", "value":"Seconds"},
			{"text":"Minutes", "value":"Minutes"},
			{"text":"Hours","value":"Hours"},
			{"text":"Days","value":"Days"}
		],
		"tooltip":"Specify the time units for all time based attribute values"
	},
	"distance_units":{
		"display":true,
		"inputType":"select",
		"labelText":"Distance Units",
		"value": "Kilometers",
		"domain": [
			{"text":"Miles","value":"Miles"},
			{"text":"Kilometers","value":"Kilometers"},
			{"text":"Feet","value":"Feet"},
			{"text":"Yards","value":"Yards"},
			{"text":"Meters","value":"Meters"},
			{"text":"Nautical Miles","value":"Nautical Miles"}
		],
		"tooltip":"Specify the distance units for all distance based attribute values"
	},
	"uturn_policy":{
		"display":false,
		"inputType": "select",
		"labelText": "U-Turn Policy",
		"value": "ALLOW_UTURNS",
		"domain": [
			{"text":"Allow U-turns", "value":"ALLOW_UTURNS"},
			{"text":"Allow at dead ends and intersections only", "value":"ALLOW_DEAD_ENDS_AND_INTERSECTIONS_ONLY"},
			{"text":"Allow at dead ends only", "value":"ALLOW_DEAD_ENDS_ONLY"},
			{"text":"Do not allow U-turns","value":"NO_UTURNS"}
		],
		"tooltip": "Restrict the use of U-turns. If travel modes are set, this value is overridden. See REST API docs."
	},
	"time_window_factor":{
		"display":true,
		"inputType":"select",
		"labelText":"Time Window Factor",
		"value": "Medium",
		"domain": [
			{"text": "Low", "value":"Low"},
			{"text": "Medium", "value": "Medium"},
			{"text":"High", "value":"High"}
		],
		"tooltip": "Rate the importance of honoring time windows without causing violations"
	},
	"spatially_cluster_routes":{
		"display":true,
		"inputType":"select",
		"labelText":"Spatially Cluster Routes",
		"value":"true",
		"domain": [
			{"text":"True", "value":"true"},
			{"text": "False", "value":"false"}
		],
		"tooltip":"Specify is orders assigned to an individual route are spatially clustered"
	},
	"route_zones":{
		"display":true,
		"inputType": "text",
		"labelText":"Route Zones",
		"value":"",
		"tooltip":"Enter URL to REST query to specify areas that delineate work territories for given routes"
	},
	"route_renewals":{
		"display":true,
		"inputType":"text",
		"labelText":"Route Renewals",
		"value":"",
		"tooltip":"Enter URL to REST query to specify intermidate depots that routes can visit to reload cargo"
	}
}