<template>
  <div id="app">
    <div>
      <b-navbar type="dark" variant="dark" class="navbar-expand">
        <b-navbar-nav>
          <b-nav-item to="/form" >Form</b-nav-item>
          <b-nav-item to="/map" >Map</b-nav-item>
          <b-nav-item href="https://www.arcgis.com/sharing/rest/oauth2/authorize?client_id=cDEbMgKnRUKN85YW&response_type=token&redirect_uri=http://localhost:8080/redirect.html?" hidden>get</b-nav-item>
        </b-navbar-nav>
        <b-navbar-nav class="ml-auto">
          <b-nav-item @click="showArc = !showArc" :active="showArc">ArcGIS Online</b-nav-item>
          <b-nav-item-dropdown text="Job History" right>
            <b-dropdown-item v-for="(info, job) in jobHistory" @click="changeJob(info,job)">{{info.date}}</b-dropdown-item>
          </b-nav-item-dropdown>
        </b-navbar-nav>
      </b-navbar>
      <ArcGISOnline v-show="showArc" :display="showArc" id="arcgisonline" searchType="Feature Service" @addItem="handleAGOL"></ArcGISOnline>
    </div>
    <AlertOverlay />
    <router-view class="view" ref="appBody"/>
  </div>
</template>

<script>

import ArcGISOnline from '@/components/ArcGISOnline.vue'
import AlertOverlay from '@/components/AlertOverlay.vue'
import * as esriLoad from 'esri-loader'
import {get} from 'vuex-pathify'

export default {
  name: "app",
  components: {
    ArcGISOnline,
    AlertOverlay
  },
  data: function() {
    return {
      showArc: false,
    }
  },
  computed: {
    searchType: function() {
      if (this.$route.name == 'Form') return 'Table'
      else if (this.$route.name == 'Map') return 'Feature Service'
    },
    jobHistory: get('jobHistory')
  },
  created: function() {
    this.$store.set('jobHistory', null)
    const formDefault = localStorage.getItem('formDefault');
    if (formDefault) {
      let tmp = JSON.parse(formDefault);
      this.$store.set('requiredParam', tmp.defReq);
      this.$store.set('optionalParam', tmp.defOpt);
    }
  },
  methods: {
    handleAGOL: function(itemId) {
      this.$refs.appBody.loadItem(itemId);
      this.showArc = false;
    }, 
    changeJob: function(info, job) {
      this.$store.set('newJob', {job:job,info:info})
    },
    registerToken: function(pStr) {
      var out = {};
      pStr.split('&').forEach((elem)=>{
        let q = elem.split('=');
        out[q[0]]=q[1];
      });
      esriLoad.loadModules(["esri/identity/IdentityManager"]).then(([esriId])=>{
        esriId.registerToken({
          server: 'https://arcgis.com/sharing/rest',
          token: out.access_token
        });
      });
    },
  }
}
</script> 

