  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}

#arcgisonline {
  z-index: 99 !important;
  position: fixed;
  width: 30vh;
  top: 56px;
  right: 0px;
}

.view {
  position: relative;
}


</style>
