import Vue from 'vue'
import Vuex from 'vuex'
import pathify from 'vuex-pathify'
import {make} from 'vuex-pathify'
import requiredParam from '@/assets/requiredParameters.js'
import optionalParam from '@/assets/optionalParameters.js'

Vue.use(Vuex);

var rp = JSON.parse(JSON.stringify(requiredParam));
var op = JSON.parse(JSON.stringify(optionalParam));

console.log(requiredParam,optionalParam);
console.log(rp,op);

var state = {
	requiredParam: rp,
	optionalParam: op,
	jobId: null,
	jobHistory: null,
	jobStatus: null,
	messages: [],
	geocodeReq: null,
	geocodeModal: false,

};

const mutations = make.mutations(state);

export default new Vuex.Store({
  plugins: [pathify.plugin],
  state,
  mutations,
  
  getters: {
  	orderLen: (state)=>{
  		console.log(state)
  		return Object.keys(state.requiredParam.Order).length
  	},
  	
  	depotLen: (state)=>{
  		return Object.keys(state.requiredParam.Depot).length
  	},
  	
  	routeLen: (state)=>{
  		return Object.keys(state.requiredParam.Route).length
  	},
  	
  },
  actions: {
  	setProcessJob({commit, dispatch, state}, promise) {
  		commit('SET_JOB_STATUS', 'processing')
  		promise.then((response)=>{
  			console.log(response)
  			var wasError = response.messages.filter((elem)=>{
  				var messages = state.messages;
  				messages.push(elem.description)
  				commit('SET_MESSAGES', messages)
  				return (elem.description.includes('030088') || elem.description.includes('030092'))
  			});
  			if (response.jobStatus == 'job-succeeded' && wasError.length == 0) {
  				commit('SET_JOB_STATUS', 'success')
  				commit('SET_JOB_ID', response.jobId)
  				dispatch('setJobHistory', response.jobId)
  			} else if (response.jobStatus == 'job-failed' || wasError.length > 0) {
  				commit('SET_JOB_STATUS', 'failed')
  			}
  		})
  	},
  	setJobHistory({commit, state}, jobId) {
  		var jobhistory = localStorage.getItem('jobhistory');
	    if (jobhistory) {
	      	let newjobhistory = JSON.parse(jobhistory);
	        if (jobId && newjobhistory) newjobhistory[jobId] = {date: Date(), requiredParam:state.requiredParam, optionalParam:state.optionalParam};
        	for (var key in newjobhistory) {
          		if ((new Date().getTime() - new Date(newjobhistory[key]["date"]).getTime()) >= 86400000) delete newjobhistory[key];
        	}
	        jobhistory = newjobhistory;
	    } else if (jobId) {
	       	jobhistory = {[jobId]: {date: Date(), requiredParam:state.requiredParam, optionalParam:state.optionalParam}};
	    } else {
	    	jobhistory = null
	    }
	    localStorage.setItem('jobhistory', JSON.stringify(jobhistory));
	    commit('SET_JOB_HISTORY', jobhistory);
  	},
    setNewJob({commit}, {job,info}) {
      commit('SET_JOB_ID', job);
      commit('SET_REQUIRED_PARAM', info.requiredParam);
      commit('SET_OPTIONAL_PARAM', info.optionalParam);
    },
    setClear({commit}) {
      commit('SET_REQUIRED_PARAM', JSON.parse(JSON.stringify(requiredParam)))
      commit('SET_OPTIONAL_PARAM', JSON.parse(JSON.stringify(optionalParam)))	
    }
  }
 
})


