<template>
	<transition name="slide-fade">
		<div>
			<b-form-input type="text" v-model="queryStr" ref="input"></b-form-input>
			<b-form-select :options="privacy" v-model="selectedPrivacy"></b-form-select>
			<b-list-group>
				<b-list-group-item v-for="(elem, index) in results" :key="index" @click="$emit('addItem',elem.id)">
					<img :src="elem.thumbnailUrl"/>
					<h5 class="noselect">{{elem.title}}</h5>
					<p class="noselect">{{elem.description}}</p>
				</b-list-group-item>
			</b-list-group>
		</div>
	</transition>
</template>

<script>
import * as esriLoad from 'esri-loader'
import * as debounce from 'lodash.debounce'

export default {
	name: 'ArcGISOnline',
	props: {
		display: Boolean
	},
	data: function() {
		return {
			queryStr: "",
			results: null,
			privacy: [
				{text: "Public", value:"public"},
				{text: "Organization", value:"org"},
				{text: "Private", value:"private"},
				{text: "Shared", value:"shared"}
			],
			selectedPrivacy: 'public'
		}
	},
	watch: {
		queryStr: function(newQuery, oldQuery) {
			if (this.queryStr) this.debounceSearch();
		},
		selectedPrivacy: function(newQuery, oldQuery) {
			if (this.queryStr) this.debounceSearch();
		},
		display: function() {
			if (this.display) this.$refs.input.focus();
		}
	},
	created: function() {
		this.debounceSearch = debounce(this.search, 500);
		esriLoad.loadModules(["esri/portal/Portal"]).then(([Portal])=>{
			var portal = new Portal({
				authMode: 'immediate'
			});
			portal.load().then(()=>{
				this.portal = portal;
				console.log(portal);
			});

		});
	},
	computed: {
		queryParams: function() {
			return {
				query: `title:${this.queryStr}* AND type:'Feature Service AND access:${this.selectedPrivacy}`,
				sortField: 'type',
				sortOrder: 'desc',
				num: 10
			}
		},
	},
	methods: {
		search: function() {
			console.log('starting search');
			this.portal.queryItems(this.queryParams).then((queryResult)=>{
				this.results = queryResult.results;
				console.log(queryResult);
			});
		},
	},

}
</script>
<style>
.slide-fade-enter-active {
  transition: all .4s ease;
}
.slide-fade-leave-active {
  transition: all .4s ease;
}
.slide-fade-enter, .slide-fade-leave-to
/* .slide-fade-leave-active below version 2.1.8 */ {
  transform: translateX(100%);
}

h5 {
	overflow-wrap: break-word;
}

.noselect {
  -webkit-touch-callout: none; /* iOS Safari */
    -webkit-user-select: none; /* Safari */
     -khtml-user-select: none; /* Konqueror HTML */
       -moz-user-select: none; /* Firefox */
        -ms-user-select: none; /* Internet Explorer/Edge */
            user-select: none; /* Non-prefixed version, currently
                                  supported by Chrome and Opera */
}

</style>